<template>
    <el-dialog
        :title="isEdit ? '' : ''"
        class="catalog-dialog"
        width="1000px"
        :visible.sync="formVisible"
        @close="resetForm"
    >
        <el-form
            :model="form"
            v-loading="loading && isEdit"
            label-position="left"
            label-width="120px"
            :rules="rules"
            ref="form"
        >
            <el-form-item label="" prop="name">
                <el-input
                    v-model.trim="form.name"
                    placeholder=""
                    maxlength="50"
                ></el-input>
            </el-form-item>
            <el-form-item label="" prop="alias">
                <el-input
                    v-model.trim="form.alias"
                    placeholder=""
                    maxlength="3"
                ></el-input>
            </el-form-item>
            <el-form-item label="" prop="desc">
                <el-input
                    type="textarea"
                    placeholder=""
                    :autosize="{ minRows: 3, maxRows: 6 }"
                    :maxlength="500"
                    v-model.trim="form.desc"
                ></el-input>
            </el-form-item>
            <el-form-item label="" prop="vedio">
                <div class="single  upload-vedio-container">
                    <div
                        :title="
                            uploadlingVideo
                                ? ''
                                : ''
                        "
                        class="video-preview"
                    >
                        <div
                            v-if="form.vedio || currentFileName"
                            class="has-border"
                            @click="selectFile"
                            :class="uploadlingVideo && 'disabled'"
                        >
                            <span>{{ currentFileName }}</span>
                            <strong v-if="progress > 0"
                                > {{ progress }}%</strong
                            >
                            <el-button
                                size="small"
                                :disabled="uploadlingVideo"
                                type="primary"
                                ></el-button
                            >
                        </div>
                        <el-button
                            size="small"
                            v-else
                            @click="selectFile"
                            type="primary"
                            ></el-button
                        >
                    </div>
                    <input
                        type="file"
                        id="upload"
                        style="display: none"
                        accept="video/*"
                        @change="uploadfile"
                    />
                </div>
            </el-form-item>
            <el-form-item label="" prop="play_time">
                <el-time-picker
                    v-model="form.play_time"
                    value-format="HH:mm:ss"
                    placeholder=""
                >
                </el-time-picker>
            </el-form-item>
            <el-form-item prop="ppt">
                <span slot="label">
                    <el-tooltip
                        class="item"
                        effect="dark"
                        content=""
                        placement="bottom"
                    >
                        <span
                            >PPT
                            <i class="el-icon-info"></i>
                        </span>
                    </el-tooltip>
                </span>
                <el-upload
                    accept="image/gif,image/jpeg,image/jpg,image/png,image/svg"
                    class="imglist-container"
                    :action="action"
                    :data="imgData"
                    name="media"
                    multiple
                    :show-file-list="false"
                    :before-upload="beforeUpload"
                    :on-success="onUploadSuccess"
                    :on-error="onUploadError"
                >
                    <template v-if="form.ppt.length">
                        <draggable
                            v-model="form.ppt"
                            class="drag-container"
                            @end="dragEnd"
                            :options="{ draggable: '.draggable-item' }"
                        >
                            <div
                                v-for="(img, index) in form.ppt"
                                class="imglist-item draggable-item"
                                :class="
                                    error.filter(e => e === index + 1).length &&
                                        'is-error'
                                "
                                @click.stop.prevent="selectImg(img, index)"
                                :key="img.id"
                            >
                                <img :src="img.media_real_url" alt="" />
                                <i
                                    class="el-icon-circle-close delete"
                                    @click.stop.prevent="delImg(img, index)"
                                ></i>
                                <div v-if="activeID === img.media_id">
                                    <div class="mask"></div>
                                    <i class="el-icon-check check"></i>
                                </div>
                            </div>
                            <div class="imglist-item noimg">
                                <i class="el-icon-plus plus"></i>
                            </div>
                        </draggable>
                    </template>
                    <div class="imglist-item noimg" v-else>
                        <i class="el-icon-plus plus"></i>
                    </div>
                </el-upload>

                <div style="margin-top: 10px" v-if="form.ppt.length">
                    <div v-for="(item, index) in form.ppt" :key="index">
                        <div v-show="activeID === item.media_id">
                            <el-time-picker
                                is-range
                                size="medium"
                                v-model="pptTime[index]"
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                                value-format="HH:mm:ss"
                                placeholder=""
                            >
                            </el-time-picker>
                            <el-button
                                title="ppt"
                                @click="addTime(index)"
                                size="medium"
                                ></el-button
                            >
                        </div>
                    </div>
                </div>
            </el-form-item>
        </el-form>

        <div slot="footer">
            <el-button @click="cancel" size="medium"></el-button>
            <el-button
                type="primary"
                :loading="btnloading"
                size="medium"
                @click="save"
                ></el-button
            >
        </div>
    </el-dialog>
</template>

<script>
import { uploadImg } from 'src/api/common'
import {
    addCatalog,
    updateCatalog,
    getCatalogDetail,
    getPolicy
} from 'src/api/lesson'
import cookie from 'js-cookie'
import draggable from 'vuedraggable'

const pptTimeDefault = ['00:00:00', '00:00:00']

export default {
    components: {
        draggable
    },
    data() {
        const vaildppt = (rule, value, callback) => {
            if (value.length) {
                const result = []
                value.forEach((item, index) => {
                    if (!item.start_time || !item.end_time) {
                        result.push(index + 1)
                    }
                })
                this.error = result

                if (result.length) {
                    callback(
                        new Error(`${result.join(',')}`)
                    )
                } else {
                    callback()
                }
            } else {
                callback(new Error('ppt'))
            }
        }

        return {
            action: uploadImg,
            imgData: { media_type: 1 },
            vedioData: { media_type: 3 },
            form: {
                name: '', // 
                alias: '', // 
                vedio: '', // 
                vedio_url: '',
                desc: '', // 
                ppt: []
            },
            formVisible: false,
            rules: {
                name: {
                    message: '',
                    required: true,
                    trigger: ['blur']
                },
                alias: {
                    message: '',
                    required: true,
                    trigger: ['blur']
                },
                vedio: {
                    message: '',
                    required: true,
                    trigger: ['blur']
                },
                ppt: { validator: vaildppt, trigger: ['blur', 'change'] },
                play_time: {
                    message: '',
                    required: true,
                    trigger: ['blur']
                }
            },
            loading: false,
            btnloading: false,
            isEdit: false,
            pptTime: [], // PPTppttime
            fileList: [],
            activeID: '',
            activeIndex: 0,
            error: [],
            progress: 0,
            currentFileName: '',
            uploadlingVideo: false,
            ossInfo: {}
        }
    },
    created() {
        this.getPolicy()
    },
    methods: {
        async getPolicy() {
            const SSTToken = cookie.get('SSTToken')
            if (!SSTToken || !Object.keys(this.ossInfo).length) {
                // tokenossInfo
                const res = await getPolicy()
                this.ossInfo = res
                this.saveSSTtToken()

                return res
            }
            return this.ossInfo || {}
        },
        saveSSTtToken() {
            const { SecurityToken, deploy } = this.ossInfo
            const { expire } = deploy || {}
            cookie.set('SSTToken', SecurityToken, {
                expires: new Date(expire * 1000)
            })
        },
        selectFile() {
            if (this.uploadlingVideo) {
                return
            }
            document.getElementById('upload').click()
        },
        async uploadfile() {
            const file = document.getElementById('upload').files[0]
            this.currentFileName = file.name
            const ossInfo = await this.getPolicy() // tokenossInfo
            const { AccessKeyId, SecurityToken, deploy } = ossInfo
            const { policy, signature, host, dir } = deploy || {}

            const fd = new FormData()
            fd.append('OSSAccessKeyId', AccessKeyId)
            fd.append('policy', policy)
            fd.append('signature', signature)
            fd.append('x-oss-security-token', SecurityToken)
            fd.append('key', `${dir}/${file.name}`)
            fd.append('success_action_status', 200)
            fd.append('file', file, file.name)

            const xhr = new XMLHttpRequest()

            this.uploadlingVideo = true

            xhr.open('post', host, true)
            xhr.upload.addEventListener(
                'progress',
                evt => {
                    this.progress = Math.round((evt.loaded * 100) / evt.total)
                },
                false
            )
            xhr.addEventListener(
                'load',
                e => {
                    if (e.target.status !== 200) {
                        this.$message.error('')
                        return
                    }
                    if (e.target.status === 200) {
                        this.$message.success('')
                        this.form.vedio = `${dir}/${file.name}`
                    }
                    this.uploadlingVideo = false
                },
                false
            )
            xhr.send(fd)
        },
        isPng(src) {
            return /^https:\/\/.*?(gif|png|jpg)$/.test(src)
        },
        // 
        countSec(time) {
            const arr = time.split(':')
            return arr[0] * 3600 + arr[1] * 60 + arr[2]
        },
        nextVaild(index) {
            const next = index + 1
            const ppt = this.form.ppt
            if (
                ppt[next].start_time &&
                this.countSec(ppt[next].start_time) <
                    this.countSec(ppt[index].end_time)
            ) {
                // 
                return 'PPT'
            }
            return false
        },

        preVaild(index) {
            const preIndex = index - 1
            const ppt = this.form.ppt
            if (
                ppt[preIndex].start_time &&
                this.countSec(ppt[preIndex].end_time) >
                    this.countSec(ppt[index].start_time)
            ) {
                // 
                return 'PPT'
            }
            return false
        },

        vaild(index) {
            let result1 = true

            if (this.form.ppt.length <= 1) {
                // PPT
                return result1
            }
            if (index === 0) {
                const result = this.nextVaild(index)
                if (result) {
                    this.$message.error(result)
                    result1 = false
                }
            } else if (index === this.form.ppt.length - 1) {
                const result = this.preVaild(index)
                if (result) {
                    this.$message.error(result)
                    result1 = false
                }
            } else {
                const result = this.preVaild(index) || this.nextVaild(index)
                if (result) {
                    this.$message.error('PPT')
                    result1 = false
                }
            }
            return result1
        },

        // 
        addTime(index) {
            if (!this.activeID) {
                this.$message.error('PPT')
                return
            }

            if (this.pptTime[index][0] === this.pptTime[index][1]) {
                this.$message.error('PPT')
                return
            }

            this.form.ppt = this.form.ppt.map(item => {
                if (item.media_id === this.activeID) {
                    item.start_time = this.pptTime[index][0]
                    item.end_time = this.pptTime[index][1]
                }
                return item
            })
            this.$refs.form.validateField('ppt')

            // 
            this.vaild(index)
        },
        onSuccessVedio(type, file) {
            if (type === 'change') {
                this.hasChange = true
                if (this.uploaderInstance) {
                    const files = this.uploaderInstance.files
                    this.uploaderInstance.splice(0, files.length - 1)
                }
            } else {
                this.$message.success('')
                this.form.vedio = `video/${file.name}`
            }
        },
        handlePictureCardPreview() {
            this.pptTime = []
            // console.log(file, file1)
        },
        handleRemoveImg() {
            // console.log(file, fileList);
        },
        handleRemove() {
            // console.log(file, fileList);
        },
        open(item) {
            this.formVisible = true
            this.isEdit = !!item
            this.hasChange = false

            if (this.isEdit) {
                this.loading = true
                getCatalogDetail(this.$route.params.id, item.id)
                    .then(res => {
                        this.form = {
                            ...res,
                            desc: res.remark,
                            vedio: res.video_media_id, // 
                            vedio_url: res.video_url,
                            ppt: res.ppt.map(list => {
                                list.media_real_url = list.url
                                return list
                            })
                        }
                        this.currentFileName = res.video_media_id.slice(6)
                        this.activeID = res.ppt[0].media_id
                        this.activeIndex = 0
                        this.pptTime = res.ppt.map(pptitem =>
                            `${pptitem.start_time},${pptitem.end_time}`.split(
                                ','
                            )
                        )
                    })
                    .finally(() => {
                        this.loading = false
                    })
            } else {
                this.resetForm()
                this.pptTime = []
                this.form = {
                    alias: '',
                    name: '', // 
                    vedio: '', // 
                    vedio_url: '',
                    desc: '', // 
                    ppt: []
                }
                this.currentFileName = ''
            }
            this.fileList = []
            this.uploadlingVideo = false
        },
        close() {
            this.formVisible = false
        },
        resetForm() {
            this.uploaderInstance && this.uploaderInstance.destroy()
            this.uploaderInstance = ''
            this.$refs.form && this.$refs.form.resetFields()
        },
        cancel() {
            this.formVisible = false
        },
        validAllImg() {
            let result = true
            for (let i = 0; i < this.form.ppt.length; i++) {
                if (!this.vaild(i)) {
                    // false
                    result = false
                    break
                }
            }
            return result
        },
        save() {
            this.$refs.form.validate(valid => {
                if (valid) {
                    // 
                    if (!this.validAllImg()) {
                        this.$message.error('PPT')
                        return
                    }
                    if (this.btnloading) {
                        return
                    }
                    this.btnloading = true
                    if (!this.isEdit) {
                        addCatalog(this.$route.params.id, this.form)
                            .then(() => {
                                this.$message.success('')
                                this.$emit('onsuccess')
                                this.formVisible = false
                            })
                            .catch(e => {
                                this.$message.error(e.message)
                            })
                            .finally(() => {
                                this.btnloading = false
                            })
                    } else {
                        updateCatalog(
                            this.$route.params.id,
                            this.form.id,
                            this.form
                        )
                            .then(() => {
                                this.$message.success('')
                                this.formVisible = false
                                this.$emit('onsuccess')
                            })
                            .catch(e => {
                                this.$message.error(e.message)
                            })
                            .finally(() => {
                                this.btnloading = false
                            })
                    }
                }
            })
        },
        beforeUpload(file) {
            if (file.size >= 2 * 1024 * 1024) {
                this.$message.error('PPT2M')
                return false
            }
            return true
        },
        onUploadSuccess(res) {
            if (res.code === 200) {
                this.form.ppt.push(res.data)
                this.$message.success('')
                this.pptTime.push(pptTimeDefault)
            } else {
                this.$message.error(`${res.msg}`)
            }
        },
        onUploadError() {
            this.$message.error('')
        },
        // ppt
        findNobindTimeInex(i) {
            return this.form.ppt.findIndex(
                (item, index) => !item.start_time && index < i
            )
        },
        selectImg(img, index) {
            // 
            const i = this.findNobindTimeInex(index)
            if (i > -1) {
                this.$message.info(
                    'pptppt'
                )
                this.selectImg(this.form.ppt[i], i)
                return
            }

            if (img.start_time) {
                // ,
                const result = []
                result.push(img.start_time)
                result.push(img.end_time)
                this.pptTime[index] = result
                this.activeID = img.media_id
                this.activeIndex = index
                return
            }

            // 00:00:00 - 00:08:0000:08:00 
            const preIndex = index - 1
            this.pptTime[index] =
                preIndex > -1
                    ? [
                          this.form.ppt[preIndex].end_time,
                          this.form.ppt[preIndex].end_time
                      ]
                    : pptTimeDefault
            this.activeID = img.media_id
            this.activeIndex = index
        },
        // 
        dragEnd() {
            //
            this.$message.info(
                'PPTPPT'
            )
            this.selectImg(this.form.ppt[0], 0)
        },
        delImg(img, index) {
            this.form.ppt = this.form.ppt.filter(
                item => item.media_id !== img.media_id
            )
            this.pptTime = this.pptTime.filter((item, i) => i !== index)
            if (index !== this.form.ppt.length - 1 && img.start_time) {
                // PPT
                this.$message.info(
                    'PPTPPT'
                )
                this.selectImg(this.form.ppt[0], 0)
            }
        }
    }
}
</script>

