<template>
    <div class="lesson-form" v-loading="loadingDetail">
        <el-form
            :model="form"
            :rules="rules"
            label-position="left"
            label-width="100px"
            ref="form"
        >
            <el-form-item label="" prop="name">
                <el-input
                    v-model.trim="form.name"
                    placeholder=""
                    maxlength="50"
                ></el-input>
            </el-form-item>
            <el-form-item label="" prop="category">
                <el-select v-model="form.category" placeholder="">
                    <el-option
                        :label="option.categoryName"
                        v-for="option in options"
                        :key="option.categoryId"
                        :value="option.categoryId"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="lector">
                <el-input
                    v-model.trim="form.lector"
                    placeholder=""
                    maxlength="50"
                ></el-input>
            </el-form-item>
            <el-form-item label="" prop="img">
                <img-loader
                    v-model="form.imgUrl"
                    :action="action"
                    :data="imgData"
                    :before-upload="beforeUpload"
                    @success="onUploadSuccess"
                    @error="onUploadError"
                >
                </img-loader>
            </el-form-item>
            <el-form-item label="" prop="desc">
                <el-input
                    type="textarea"
                    placeholder=""
                    :autosize="{ minRows: 3, maxRows: 6 }"
                    :maxlength="500"
                    v-model.trim="form.desc"
                ></el-input>
            </el-form-item>
        </el-form>
        <div class="submit-btn">
            <el-button type="primary" @click="save" size="medium"
                ></el-button
            >
        </div>
    </div>
</template>

<script>
import { uploadImg } from 'src/api/common'
import {
    getLessonDetail,
    getLessonCategory,
    addLesson,
    updateLesson
} from 'src/api/lesson'

export default {
    data() {
        return {
            action: uploadImg,
            imgData: { media_type: 1 },
            form: {
                id: 1,
                img: '', // 
                imgUrl: '',
                name: '', // 
                category: '', // 
                number: '', // 
                lector: '', // 
                desc: '' // 
            },
            rules: {
                name: {
                    message: '',
                    required: true,
                    trigger: ['blur']
                },
                category: {
                    message: '',
                    required: true,
                    trigger: ['blur']
                },
                lector: {
                    message: '',
                    required: true,
                    trigger: ['blur']
                },
                img: {
                    message: '',
                    required: true,
                    trigger: ['blur', 'change']
                }
            },
            options: [
                {
                    categoryId: 1, // id
                    categoryName: '' // 
                }
            ],

            loadingDetail: false,
            btnloading: false
        }
    },
    methods: {
        beforeUpload() {
            //
        },
        onUploadSuccess(res) {
            if (res.code === 200) {
                this.form.imgUrl = res.data.media_real_url
                this.form.img = res.data.media_id
                this.$message.success('')
            } else {
                this.$message.error(`${res.msg}`)
            }
        },
        onUploadError() {
            this.$message.error('')
        },
        save() {
            this.$refs.form.validate(valid => {
                if (valid) {
                    if (this.btnloading) {
                        return
                    }
                    this.btnloading = true
                    if (!this.$route.params.id) {
                        addLesson(this.form)
                            .then(() => {
                                this.$message.success('')
                                this.$emit('onsuccess')
                            })
                            .catch(e => {
                                this.$message.error(e.message)
                            })
                            .finally(() => {
                                this.btnloading = false
                            })
                    } else {
                        updateLesson(this.$route.params.id, this.form)
                            .then(() => {
                                this.$message.success('')
                                this.$emit('onsuccess')
                            })
                            .catch(e => {
                                this.$message.error(e.message)
                            })
                            .finally(() => {
                                this.btnloading = false
                            })
                    }
                }
            })
        },
        fetchDetail() {
            if (this.loadingDetail) {
                return
            }
            this.loadingDetail = true

            getLessonDetail(this.$route.params.id)
                .then(res => {
                    this.form = {
                        ...res,
                        imgUrl: res.img,
                        img: res.img_media_id
                    }
                    this.$refs.form.clearValidate()
                })
                .catch(e => {
                    this.$message.error(e.message)
                })
                .finally(() => {
                    this.loadingDetail = false
                })
        },
        reset() {
            this.$refs.form && this.$refs.form.resetFields()
        }
    },
    created() {
        if (this.$route.params.id) {
            this.fetchDetail()
        }
        getLessonCategory()
            .then(res => {
                this.options = res
            })
            .catch(e => {
                this.$message.error(e.message)
            })
    }
}
</script>

<style lang="less" scoped>
.lesson-form {
    .el-input,
    .el-textarea,
    .el-select {
        width: 400px;
    }
}
.submit-btn {
    margin: 30px 0 10px;
    text-align: right;
    button {
        min-width: 100px;
    }
}
</style>
